.bike {
  display: block;
  margin: 0 auto;

  &__photo {
    display: block;
    width: 100%;

    @include onTablet {
      max-width: 392px;
    }

    @include onDesktop {
      max-width: 324px;
    }
    
    @include onDesktop {
      transition: transform 1s;

      &:hover {
        transform: scale(1.2);
      }
    }
  }

  &__title {
    padding-top: 32px;
    font-weight: 700;
    font-size: 24px;
  }

  &__description {
    padding-top: 8px;
    font-weight: 400;
    font-size: 16px;
  }
  &__price {
    display: block;
    width: 100%;
    padding-top: 16px;
    font-weight: 700;
    font-size: 24px;
  }
}
